import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';

function Header() {
  const [isOpen, setIsOpen] = useState(false); // Dropdown visibility state
  const [isActive, setIsActive] = useState(false); // Active class state
  const [searchText, setSearchText] = useState(''); // State for search input
  const dropdownRef = useRef(null); // Ref for dropdown
  const navigate = useNavigate(); // Navigation hook for redirect

  const toggleDropdown = () => {
    setIsOpen((prev) => !prev);
  };

  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsOpen(false);
    }
  };

  useEffect(() => {
    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  const handleMenuClick = () => {
    setIsActive((prev) => !prev);
    document.body.classList.toggle('active');
  };

  useEffect(() => {
    const nav = document.querySelector('nav');
    if (nav) {
      if (isActive) {
        nav.classList.add('active');
      } else {
        nav.classList.remove('active');
      }
    }
  }, [isActive]);

  // Function to handle search input
  const handleInputChange = (event) => {
    setSearchText(event.target.value);
  };

  // Function to handle search submission
  const handleSearch = (event) => {
    event.preventDefault();
    if (searchText.trim()) {
      // Redirect to the SearchResults page with the search query
      navigate(`/search?query=${searchText.trim()}`);
    } else {
      alert('Please enter a search term.');
    }
  };

  return (
    <header className="bg-white">
      <div className="container mx-auto max-w-6xl flex flex-row py-5 px-12 items-center justify-between border-b-2">
        <a href="#" className="h-menu" onClick={handleMenuClick}>
          <i className="bi bi-list"></i>
        </a>
        <a href="/" className="logo text-black text-3xl font-extrabold">
          <h1>SHOP.CO</h1>
        </a>
        <nav>
          <ul className="flex flex-row items-center gap-10">
            <li className="dropdown flex align-middle gap-5" ref={dropdownRef}>
              {isOpen && (
                <ul className="links hidden">
                  <li><a href="#">Item 1</a></li>
                  <li><a href="#">Item 2</a></li>
                  <li><a href="#">Item 3</a></li>
                  <li><a href="#">Item 4</a></li>
                  <li><a href="#">Item 5</a></li>
                </ul>
              )}
            </li>
            <li><a href="/store">Store</a></li>
            <li><a href="/#categories">Categories</a></li>
            <li><a href="/#">About Us</a></li>
          </ul>
        </nav>
        <form onSubmit={handleSearch} className="search-box pt-1 pb-1 rounded-2xl border-solid bg-gray-200 px-5 gap-2 py-2">
          <button>
            <i className="bi bi-search pointer-events-auto"></i>
          </button>
          <input
            type="search"
            value={searchText}
            onChange={handleInputChange}
            className="bg-transparent px-5 pr-20 border-none focus:border-none outline-none focus:outline-none"
            placeholder="Search for products..."
          />
        </form>
        <div className="user flex align-middle gap-5">
          <a href="/shopping-cart"><i className="bi bi-cart"></i></a>
          <a href="/login"><i className="bi bi-person-circle"></i></a>
        </div>
      </div>
    </header>
  );
}

export default Header;
