import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
// import '../sub-componennts/product-details.js'; // Assuming you're importing the JS file for some custom functionality

function LifeGraphic() {
  const location = useLocation();
  const navigate = useNavigate();
  const [quantity, setQuantity] = useState(1);
  const [selectedSize, setSelectedSize] = useState(null); // Track the selected size

  // Extract URL parameters
  const queryParams = new URLSearchParams(location.search);
  const id = queryParams.get('id');
  const title = queryParams.get('title');
  const image = queryParams.get('image');
  const price = parseFloat(queryParams.get('price'));

  // Redirect to homepage if any parameter is missing
  useEffect(() => {
    if (!id || !title || !image || !price) {
      navigate('/');
    }
  }, [id, title, image, price, navigate]);

  const handleQuantityChange = (value) => {
    if (value >= 1) {
      setQuantity(value);
    }
  };

  const handleSizeSelection = (size) => {
    setSelectedSize(size); // Set the selected size when user chooses one
  };

  // Function to add item to the cart
  const handleAddToCart = () => {
    if (!selectedSize) {
      alert("Please select a size before adding to cart.");
      return;
    }

    const cartItem = {
      id,
      title,
      image,
      price: (price * 0.9).toFixed(2), // Apply discount
      quantity,
      size: selectedSize,
    };
    // Assume a function addToCart exists for adding item to cart state
    // addToCart(cartItem);
    console.log('Item added to cart:', cartItem);
    alert(`${title} (Size: ${selectedSize}) added to cart!`);
  };

  return (
    <section className="life-graphic">
      <div className="container max-w-6xl w-max mx-auto grid grid-cols-2 gap-5 py-14 px-12">
        <div className="left grid grid-cols-[max-content_auto] mt-4 gap-4">
          <div className="col-1 grid grid-rows-3 gap-3">
            {[...Array(3)].map((_, index) => (
              <a key={index} href="#" className="w-36 h-full border-2 mb-4 block">
                <img
                  src={image}
                  alt={title}
                  className="w-full h-full object-cover px-4 py-4 opacity-75 hover:opacity-100"
                />
              </a>
            ))}
          </div>
          <div className="col-2 border-2">
            <img
              src={image}
              alt={title}
              className="w-full h-full p-12 object-contain object-center"
            />
          </div>
        </div>

        <div className="right">
          <h2 className="font-bold text-4xl text-black py-2">{title}</h2>
          <div className="icons py-4">
            {Array.from({ length: 5 }, (_, index) => (
              <span key={index}><i className="bi bi-star-fill star text-yellow-400"></i></span>
            ))}
          </div>
          <div className="d-price flex gap-2 py-4">
            <h5 className="text-xl font-bold text-gray-400"><del>${price}</del></h5>
            <span className="text-xl font-bold text-black">${(price * 0.9).toFixed(2)}</span>
            <span className="per bg-[#FF33331A] text-xs text-red-500 font-light rounded-full py-2 px-2" id="who">-%10</span>
          </div>
          <p className="text-lg font-normal text-gray-500 py-2">
            This product is perfect for any occasion. {title}.
          </p>
          <hr />

          <div className="size py-4">
            <p className="py-2 text-gray-600">Choose Size</p>
            <div className="flex gap-2">
              {['Small', 'Medium', 'Large', 'X-Large'].map((size) => (
                <label
                  key={size}
                  htmlFor={size.toLowerCase()}
                  className={`size bg-gray-200 text-black font-light rounded-full px-4 py-2 cursor-pointer ${selectedSize === size.toLowerCase() ? 'bg-black text-black font-semibold' : ' hover:text-white hover:bg-black '}`}
                  onClick={() => handleSizeSelection(size.toLowerCase())} // Set size when clicked
                >
                  <span>{size}</span>
                  <input
                    type="radio"
                    name="size"
                    id={size.toLowerCase()}
                    value={size.toLowerCase()}
                    className="hidden"
                    checked={selectedSize === size.toLowerCase()} // Make sure the selected size is checked
                    readOnly
                  />
                </label>
              ))}
            </div>
          </div>
          <hr />

          <div className="add-btn grid grid-cols-[max-content_auto] gap-4 py-4 mb-6">
            <div className="qt bg-gray-200 text-base flex items-center rounded-full w-max">
              <button onClick={() => handleQuantityChange(quantity - 1)} className="subtract w-9 text-center">-</button>
              <input type="number" readOnly value={quantity} className="w-12 text-center border-none focus:outline-none bg-transparent hide-toggle" />
              <button onClick={() => handleQuantityChange(quantity + 1)} className="add w-9 text-center">+</button>
            </div>
            <button onClick={handleAddToCart} className="bg-black text-white rounded-full px-6 py-2">Add to Cart</button>
          </div>
        </div>
      </div>
    </section>
  );
}

export default LifeGraphic;
